.v-img-box {
	position: relative;
	display: block;
	padding-bottom: 70%;
	height: 0;
	border-radius: 4px;
	background: $white;
	box-shadow: $container-shadow;

	@include hover {
		.v-img-box__picture {
			&:after {
				opacity: 1;
			}
		}

		.v-img-box__btn {
			opacity: 1;
		}
	}

	&__picture {
		position: absolute;
		top: 15px;
		left: 15px;
		right: 15px;
		bottom: 15px;
		border-radius: 4px;
		@include cover;
		@include vertical;

		@include phone {
			margin: -5px;
		}

		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: 4px;
			opacity: 0;
			background: rgba(51,51,51,0.5);
			transition: opacity 0.25s;
		}
	}

	&__btn {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle;
		padding: 9px 25px;
		border: 1px solid $white;
		border-radius: 4px;
		width: 180px;
		color: $white;
		opacity: 0;
		background: none;
		transition: opacity 0.25s;

		span,
		.icon {
			display: inline-block;
			vertical-align: top;
		}

		span {
			padding: 3px 0 2px 10px;
			@include font(12px, 700, 15px);
			text-transform: uppercase;
		}

		.icon {
			fill: currentColor;
			font-size: 20px;
		}
	}
}